<template>
	<div class="music-player">
		<div class="header">音乐播放器</div>
		<div class="main">
			<div class="cover" ref="cover_wrap" :style="{ transform: coverTransform }">
				<img ref="cover_img" src="../assets/cover.jpg" :class="{ playing: isPlaying }">
			</div>
		</div>
		<audio :src="songUrl" ref="player" @ended="end"></audio>
		<div class="tool">
			<div class="process">
				<div class="bar">
					<div class="track" :style="{ width: process }"></div>
				</div>
			</div>
			<div class="control">
				<a class="btn prev"></a>
				<a class="btn paused" :class="{ play: !isPlaying }" @click="play"></a>
				<a class="btn prev next"></a>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'musicplayer',
		data() {
			return {
				player: null,
				songUrl: 'https://icefee.github.io/blog/static/data/songs/song_1.mp3',
				isPlaying: false,
				coverTransform: '',
				processTimer: null,
				process: 0
			}
		},
		mounted() {
			this.player = this.$refs.player
		},
		methods: {
			play() {
				if(!this.isPlaying) {
					this.isPlaying = true;
					this.player.play()
					this.updateProcess(true)
				}
				else {
					let cover_wrap = this.$refs.cover_wrap;
					let cover_img = this.$refs.cover_img;

					let cover_wrap_transform = getComputedStyle(cover_wrap).transform;
					let cover_img_transform = getComputedStyle(cover_img).transform;
					this.coverTransform = cover_wrap_transform === 'none' ? cover_img_transform : cover_img_transform.concat(' ', cover_wrap_transform);
					this.isPlaying = false;
					this.player.pause()
					this.updateProcess(false)
				}
			},
			end() {
				this.isPlaying = false;
				this.process = 0;
				this.updateProcess(false)
			},
			updateProcess(flag) {
				if(flag) {
					this.processTimer = setInterval(() => {
						let player = this.player;
						let process = player.currentTime / player.duration;
						this.process = process * 100 + '%'
					}, 15)
				}
				else clearInterval(this.processTimer)
			}
		}
	}
</script>

<style scoped>
	.main {
		display: flex;
		justify-content: center;
		padding: 15vw 10vw;
	}
	.cover {
		max-width: 250px;
		border-radius: 100%;
		overflow: hidden;
		border: 20px solid rgba(255, 255, 255, .02);
	}
	.playing {
		animation: rotate 20s infinite linear;
	}
	@keyframes rotate {
		100% {
			transform: rotate(1turn)
		}
	}
	.tool {
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 0;
		padding: 10px 0;
	}
	.tool .process {
		padding: 5vw 5vw 8vw;
	}
	.process .bar {
		height: 2px;
		background: #fff;
		overflow: hidden;
	}
	.bar .track {
		width: 0;
		height: 100%;
		background: #009688;
	}
	.tool .control {
		display: flex;
		justify-content: center;
		align-items: center;
	}
	.control .btn {
		width: 15vw;
		height: 15vw;
		max-width: 60px;
		max-height: 60px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 80% auto;
	}
	.prev {
		background-image: url('../assets/icon_prev.svg');
	}
	.paused {
		margin: 0 5vw;
		background-image: url('../assets/icon_paused.svg');
	}
	.play {
		background-image: url('../assets/icon_play.svg');
	}
	.next {
		transform: rotate(180deg);
	}
</style>


